.slider {
  background: linear-gradient(to top, $yellow 28%, #eb8c38 48%, $yellow 74%);
}

.container__slider {
  position: relative;
  justify-content: center;

  @include tablets {
    width: 80%;
    align-items: center;
  }

  @include phones {
    width: 90%;
  }
}

.slider__arrow {
  position: absolute;
  top: 50%;
  z-index: 1000;

  &--left {
    left: 0;
    transform: rotate(90deg);
    left: rem(-30px);
  }
  &--right {
    right: 0;
    transform: rotate(-90deg);
    right: rem(-30px);
  }
}

.slider__list {
  display: flex;
  flex-direction: row;
  width: rem(940px);
  position: relative;
  will-change: right;
  overflow: hidden;
  z-index: 0;
  height: 100%;

  @include phones {
    width: rem(768px);
  }
}

.slider__slide {
  display: flex;
  flex-direction: row;
  align-items: center;
  min-width: 100%;
  position: relative;

  @include tablets {
    flex-direction: column;
    margin-top: rem(150px);
  }
}

.slide__row {
  flex: 1;

  @include tablets {
    flex: 0;
  }
}

.slide__row--right {
  margin-bottom: rem(20px);

  @include tablets {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: rem(7px) rem(138px);
  }
}

.slide__row-left {
  text-align: center;
}

.title__slide {
  margin-bottom: rem(37px);

  @include tablets {
    padding: 0 rem(48px);
    text-align: center;
  }

  @include small{
    margin-bottom: rem(16px);
  }
}

.slide__img {
  background: center center / contain no-repeat;

  @include phones {
    width: rem(300px);
    height: rem(225px);
  }

  @include small{
    width: rem(250px);
    height: rem(176px);
  }
}

.slide__desc {
  font-size: rem(14px);
  font-family: "Ubuntu-Light";
  color: #fff;

  @include tablets {
    text-align: center;
    font-size: rem(15px);
    margin-bottom: rem(33px);
  }

  @include phones{
      margin-bottom: 0;
  }

  @include small{
    padding: 0 rem(40px);
  }
}

.slide__order {
  display: flex;
  align-items: baseline;
  &-price,
  &-money {
    font-family: "Gagalin-Regular";
    color: $red;
  }
}

.slide__order-price {
  font-size: rem(72px);
  margin-right: rem(7px);

  @include phones {
    font-size: rem(57px);
  }
}

.slide__order-money {
  font-size: rem(30px);
  margin-right: rem(30px);

  @include phones {
    font-size: rem(28px);
  }
}
