.burger{
    background: url('../img/video-bg.png') center center / cover no-repeat;
    position: relative;

    &::after{
        content: '';
        background: url('../img/texture.png');
        background-size: cover;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
    }
}


.burger__container{
    height: 100%;
}

.burger__content{
    display: flex;
    align-items: center;
    z-index: 50;
    flex: 0.8;

    @include tablets{
        flex-direction: column;
        justify-content: center;
        flex: 0.9;
    }
}

.burger__picture-image{
    width: rem(451px);
    height: rem(420px);

    @include phones{
    width: 21rem;
    height: 18.375rem;
    }

    @include small{
    width: 19rem;
    height: 17rem;
    margin-bottom: rem(12px);
    }
}

.burger__desc{
    margin-left:  rem(11px);
    margin-top:  rem(109px);

    @include tablets{
            margin-top: rem(22px);
        text-align: center;
            padding-left: rem(31px);
            margin-left:  0;
    }

    @include phones{
        margin-top: rem(37px);
        padding: 0 rem(71px);
    }

    @include small{
        margin-top: rem(10px);
    }
}

.title--color--yellow{
    margin-bottom:  rem(23px);
}

.title__burger{

    @include phones{
        font-size: rem(72px);
    }
}

.subtitle{
    font-family: 'Lifehack-Medium';
    font-size:  rem(65px);
    color: #fff;

    @include tablets{
        padding: 0 rem(45px);
    }

    @include phones{
        font-size: rem(60px);
    }

    @include small{
        font-size:rem(40px);
    }
}

