.best{
    background: url('../img/bg2.png') center center / cover no-repeat;
}

.container__best{
    justify-content: center;
}

.title__best{
    text-align: center;

    @include small{
        margin-bottom: rem(20px);
        padding-top: rem(25px);
    }
}

.best__list{
    display: flex;

    @include phones{
        flex-direction: column;
        margin-bottom: rem(60px);
    }
}

.best__item{
    flex:1;
    margin-right: rem(36px);
    text-align: center;


    &:last-child{
        margin-right: 0;
        margin-top: rem(10px);
    }

    @include phones{
        display: flex;
        text-align: start;
        margin-bottom: rem(45px);
    }

    @include small{
     margin-bottom: rem(15px);
    }
}

.best__picture{
    margin-bottom: rem(20px);

    @include phones{
        margin:0 rem(23px) 0 rem(26px);
    }

    &-last{
        margin-bottom: rem(24px);
    }

    &-hand {
        @include phones{
            margin:0 rem(31px) 0 rem(40px);
        }
    }

    &-first{
        margin-bottom: rem(21px);
    }
}

.meat{
    height: rem(89px);
    width: rem(97px);
    fill: #E35028;
}

.hand{
    height: rem(88px);
    width: rem(73px);
}

.natural{
    width: rem(88px);
    height: rem(75px);
}

.best__desc-title{
    margin-bottom: rem(10px);
    font-family: 'Lifehack-Medium';
    font-size: rem(24px);
    color: $black;
    line-height: 1.17;

    @include phones{
        font-size: rem(27px);
        margin-bottom: rem(20px);
    }

    @include small{
        margin-bottom: rem(10px);
    }
}

p{
opacity: 0.8;
font-family: 'Ubuntu-Light';
font-size: rem(14px);
color: #636464;
line-height: 1.71;
}