.title{
    font-family: 'Gagalin-Regular';
    font-size:  rem(72px);
    color: $yellow;
    margin-bottom:  rem(55px);
    line-height:1;

    &--color{
        &--black{
            color: $black;
        }
        &--white{
            color: #fff;
        }
    }

    @include phones{
        font-size: rem(58px);
        line-height: 1.2
    }

    @include small{
        margin-bottom: 0;
        font-size: rem(45px);
    }
}