.radio__btns {
    display   : flex;
    margin-top:  rem(18px);

    @include phones{
        justify-content: center;
    }
}

.radio {
    cursor: pointer;
}

.radio__input {
    opacity : 0;
    position: absolute;

    &:checked+.radio__content {
        .radio__btn {
            &:after {
                opacity: 1;
            }
        }
    }

    &:checked+.checkbox__fake {
        .checkbox__img {
                opacity: 1;
        }
    }
}

.radio__content {
    display: flex;
    margin-right:  rem(20px);
}

.radio__btn {
    width        :  rem(15px);
    height       :  rem(15px);
    border-radius: 50%;
    border       :  rem(1px)solid #fff;
    position     : relative;
    background   : #fff;
    margin-right :  rem(10px);

    &:after {
        content      : '';
        width        :  rem(7px);
        height       :  rem(7px);
        border-radius: 50%;
        background   : $red;
        position     : absolute;
        top          : 50%;
        left         : 50%;
        transform    : translate(-50%, -50%);
        opacity      : 0;
        transition   : .3s;
    }
}

.check{
    margin-top:  rem(29px);
    margin-bottom:  rem(20px);

    @include small{
        margin: rem(11px) 0;
    }
}


.checkbox__fake {
    display: flex;

    @include phones{
        justify-content: center;
    }
}

.checkbox__fake-btn {
    width     :  rem(15px);
    height    :  rem(15px);
    background: white;
    position  : relative;
    margin-right:  rem(10px);

}
    .checkbox__img{
     position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: rem(11px);
    height: rem(9px);
    opacity: 0;
        }
        

.form__buttons{

    @include phones{
        display: flex;
        justify-content: center;
        margin-top: 28px;
    }

    @include small{
        margin-top: 0;
    }
}

.btn__form{
    margin-right:  rem(20px);
     @include phones{
        margin-left: 0;
    }
}