

.reviews__list{
    height: 100%;
    display: flex;
    flex-wrap: wrap;
}

.reviews__item{
    position: relative;
    width: 25%;
    height: 50%;

    &:hover {
        .reviews__picture{
            filter: grayscale(100%) brightness(50%);
        }
        .reviews__hover{
            opacity: 1;
        }
        
        
    }

    @include tablets{
        width: 50%;
        height: 25%;
    }
}

.reviews__picture{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: center center / cover no-repeat;
}

.reviews__hover{
    color: #fff;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
   min-height: 100%;
    transform: translate(-50%, -50%);
    background-color: #444646 .5;
    padding: rem(50px)  rem(19px)  rem(19px);
    transition: opacity .6s;

    @include tablets{
        padding: rem(35px)  rem(55px)  rem(20px);
        text-align: center;
    }
}

.reviews__hover-title{
    font-family: 'Gagalin-Regular';
    font-size:  rem(14px);
    line-height: 2;

    @include phones{
        font-size: rem(24px);
        line-height: 1.4;
    }
}

.reviews__text{
    color: #fff;

    @include phones{
        display: none;
    }
}

.btn__reviews{
    background: #2f3234;
    margin-top:  rem(17px);

    &:hover{
       background: #1f2122;
    }

    @include phones{
        display: none;
    }
}

.btn__reviews--2{
    display: none;
    
    @include phones{
        display: block;
        margin-left: rem(9px);
        font-size: rem(15px);
            margin-top: rem(20px);
    }
}


