.btn{
    padding: rem(10px) rem(15px);
    border-radius: rem(5px);
    background: $orange;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Ubuntu';
    font-size: rem(13px);

    @include phones{
        font-size: rem(15px);
    }

    @include small{
        font-size: rem(12px);
    }
}