.vk,
.fb,
.inst,
.twitter{
    fill: #575757;
    margin-right:  rem(20px);

    &:hover{
        fill:#fff;
    }
    cursor: pointer;
}

.vk{
    width:  rem(23px);
height:  rem(13px);
}

.fb{
    width:  rem(20px);
height:  rem(20px);
}

.inst{
    width:  rem(20px);
height:  rem(20px);
}

.twitter{
    width:  rem(20px);
height:  rem(16px);
margin-right: 0;
}
