.menu {
    background: url('../img/menu.png') center center / cover no-repeat;
    position  : relative;

}

.title__menu{
    position: absolute;
    top: rem(24px);
    left: rem(95px);
    
    @include tablets{
        top: rem(114px);
        left:rem(23px);
    }
}

.accordeon__menu {
    display : flex;
    position: absolute;
    right   : 0;
    bottom  : 0;
    top     : 0;
    
    &-list {
        display        : flex;
        align-items    : stretch;
        justify-content: flex-end;
        position       : relative;
        height         : 100%;
    }
    
    &-item {
        display   : flex;
        position  : relative;
        height    : 100%;

        &.active{
        .accordeon__menu-drop{
         max-width: rem(640px);
        
        @include phones{
            max-width: 18rem;
        }}
        }
    }

    &-btn {
        position           : relative;
        display            : inline-block;
        width              :  rem(80px);
        height             : 100%;
        background-size    : cover;
        background-position: center;
        background-repeat  : no-repeat;

        @include small{
            width: rem(64px);
        }
    }

}



.accordeon__menu-title {
    position        : absolute;
    top             : 100%;
    color           : #fff;
    font-size       :  rem(40px);
    font-family     : 'Gagalin-Regular';
    transform       : rotate(270deg) translateY(-50%);
    transform-origin: 0 0;
    left            : 50%;
    white-space     : nowrap;
    margin-top      :  rem(-40px);
}

.accordeon__menu-drop {
    position        : relative;
    background-color: rgba(47, 50, 52, 0.929);
    height          : 100%;
    float           : left;
    overflow        : hidden;
    max-width: 0;
    transition:linear .4s;
}

.accordeon__menu-text {
    padding:  rem(20px)  rem(32px);

    @include small{
        padding: rem(18px) rem(8px)
    }
}

.close{
    width: rem(18px);
    height: rem(18px);
    fill: #c1c2c2;
    position: absolute;
    top: rem(8px);
    right: rem(7px);
    display: none;
    cursor: pointer;
    
    @include phones{
        display: block;
    }
}