@function rem($px) {
    @return $px/16px + 0rem;
}

@mixin tablets(){
    @media screen and (max-width: 768px){
        @content;
    }
}

@mixin phones(){
    @media screen and (max-width: 480px) {
        @content;
    }
}


@mixin small(){
    @media screen and (max-width:310px){
        @content
    }
}
