.components {
  position: absolute;
  top: rem(120px);
  left: rem(26px);
  cursor: pointer;

  &:hover {
    .components__btn {
      background: $red;
      transition: 0.5s;
    }
    .components__drop {
      opacity: 1;
      top: 0;
    }

    
  }


  @include tablets{
    top: rem(-46px);
    left: rem(149px);
  }


  @include phones{
        top: rem(-74px);
    left: rem(130px);
  }
  @include small{
      left: rem(180px);
    }
}

.components__btn {
  width: rem(90px);
  height: rem(90px);
  border-radius: rem(5px);
  background-color: rgba(227, 80, 40, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &-text {
    font-family: "Gagalin-Regular";
    font-size: rem(14px);
    line-height: 2;
    color: #fff;
  }
}

.burger__white {
  width: rem(46px);
  height: rem(44px);
  top: 0;
  left: 0;
}

.components__drop {
  position: absolute;
  background: rgba(254, 248, 236, 0.9);
  opacity: 0;
  left: rem(90px);
  top: rem(-2000px);
  border-radius: rem(5px);
  min-width: rem(270px);
  transition: opacity 0.3;

  @include tablets {
    min-width: rem(342px);
  }

  @include phones{
    min-width: rem(420px);
  }

  @include small{
    min-width: rem(238px);
  }
}

.components__list {
  position: relative;
  padding: rem(20px) rem(22px);
  display: table;
  width: 100%;

  @include phones{
    padding-right: rem(85px);
  }
}

.components__item {
  display: table-row;
  font-size: rem(14px);
  font-family: "Ubuntu-Medium";

  @include tablets {
    font-size: rem(15px);
  }
}

.components__row {
  display: table-cell;
  vertical-align: middle;
}

.svg__pic {
  width: rem(44px);
  height: rem(39px);

  @include tablets {
    width: rem(46px);
    height: rem(46px);
  }
}

.components__weight {
  text-align: end;

  @include tablets {
    text-align: start;
  }
}

.drop__close {
  position: absolute;
  cursor: pointer;
  display: none;
  @include tablets {
    display: block;
    right: rem(13px);
    top: rem(13px);
  }
}

.drop__close-menu {
  fill: #eb8262;
  width: rem(23px);
  height: rem(23px);
}

.kkal {
  opacity: 0.6;
  position: absolute;
  left: rem(26px);
  bottom: rem(150px);
  display: flex;
  flex-direction: column;
  color: #fff;
  font-family: "Gagalin-Regular";

  @include tablets {
    left: auto;
    bottom: auto;
    top: rem(-46px);
    right: rem(149px);
  }

  @include phones{
     top: rem(-74px);
    right: rem(130px);
  }

  @include small{
    right: rem(190px);
  }
}

.kkal__icon {
  width: rem(60px);
  height: rem(60px);
}

.kkal__desc {
  position: absolute;
  top: 49%;
  left: 23%;
}

.kkal__figure {
  font-size: rem(20px);
  margin-bottom: rem(11px);
}

.kkal__text {
  font-size: rem(14px);
}
