.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top:  rem(12px);
    z-index: 50;
}

.btn{
    @include tablets {
        margin-left: auto;
    }
}

.logo{
     width:  rem(98px);
     height:  rem(74px);
}


.header__buttons{
    display: flex;
    align-items: center;
    font-size:  rem(13px);
    font-family: 'Ubuntu';

    @include tablets{
        display:none;
    }
}

.btn__mobile{
    display: none;
    position: relative;
    z-index: 900;
    width: rem(25px);
    height: rem(25px);
    margin-left: rem(20px);
    margin-right: 2vw;

    @include tablets{
        display: block;
    }
}

.btn_mobile-hmbrgr{
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    width: 100%;
    height: 100%;
    z-index:$z-mobile-hamburger;
}

.btn_mobile-hmbrgr-decor{
        display: inline-block;
    height: rem(3px);
    border-radius: rem(3px);
    background-color: #fff;
}

