* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  outline: none;
}

html{
    
    font-size: 16px;

    @include tablets {
        font-size: 15px;
    }

    @include phones {
        font-size: 14px;
    }

    @include small{
        font-size:12px;
    }
}

html,
body{
    height: 100%;
}


.body{
    position: relative;
}

.container{
    max-width: rem(940px);
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.wrapper{
    position: relative;
    width: 100%;
    overflow: hidden;
    height: 100%;
}

.sections{
    height: 100%;
    transition: transform 0.5s;
}


//tag style reset
h1,
h2,
h3,
h4,
h5,
h6{
    margin-top:0;
    font-weight: normal;
}

a{
    text-decoration: none;
}

table{
    border-collapse: collapse;
    border-spacing: 0;
}

 li{
    list-style-type: none;
}


footer{
    position: relative;
    width: 100%;
}

button{
    background: transparent;
    border:none;
    cursor: pointer;
    color: inherit;
}