.footer {
    position         : absolute;
    bottom           : 0;
    left             : 0;
    right            : 0;
    background-image : url('../img/footer.texture.png');
    background-repeat: no-repeat;
    background-size  : cover;
    overflow         : hidden;
    height           :  rem(60px);
    padding          :  rem(10px) 0;
    width            : 100%;

    @include phones{
        height:rem(130px);
    }

    @include small{
        height: rem(98px);
    }
}

.footer__container {
    display  : flex;
    max-width:  rem(940px);
    height   : 100%;
    margin   : 0 auto;

    @include phones{
            flex-direction: column;
    justify-items: center;
    align-items: center;
    }
}

.footer__column {
    flex: 1;

    @include phones{
        flex: 0;
        display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: rem(13px);
    }

    @include small{
        margin-bottom: 0;
    }
}

.address {
    color        : white;
    font-family  : 'Ubuntu-Light';
    font-size    :  rem(14px);
    margin-bottom:  rem(10px);

    @include phones{
        font-size: rem(16px);
    }
}

.contacts {
    font-family: 'Ubuntu-Bold';
    color      : $red;
    font-size  :  rem(16px);
    display    : flex;
    align-items: center;

    @include phones{
        font-size: rem(17px);
    }
}

.phone {
    width       :  rem(12px);
    height      :  rem(11px);
    margin-right:  rem(6px);
    fill: $red;
}

.social {
    display        : flex;
    justify-content: flex-end;
    align-items    : center;
    padding-top    :  rem(8px);
}