.popup{
    position: fixed;
    z-index: 10000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(44, 47, 49, 0.9);
    display: flex;
     justify-content: center;
     align-items: center;
     left: -100%;
     transition: opacity 0.3s;
     opacity: 0;

     &.active{
         left: 0;
         opacity: 1;
     }

     @include phones{
         &.active .reviews__popup{
             display: block;
         }
     }
}

.popup__wrap{
    background-color: #fff;
    padding: rem(26px);
    border-radius: rem(5px);
    width: rem(473px);
    position: relative;

    @include small{
        width: rem(380px);
    }
}

.close__popup{
    position: absolute;
    right: rem(21px);
    top: rem(18px);

    &-svg{
        fill: #ec8468;
    width: rem(15px);
    height: rem(15px);
    }
}

.reviews__title-popup{
    margin-bottom: rem(13px);
}

.reviews__popup{
    color: #2f3234;
    font-family: 'Ubuntu-Light';
}


