.nav{
    margin-right:  rem(15px);

    @include tablets{
        margin-right: 0;
        height: rem(420px);
    }
}

.nav__list{
    display: flex;
    align-items: center;

    @include tablets{
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
    }
}

.nav__item{
    display: flex;
    align-items: center;
    margin-right:  rem(15px);

     @include tablets{
        margin-right: 0;
        display: flex;
    flex-direction: column;
    }

    &::after{
        content: '';
        width:  rem(4px);
        height:  rem(4px);
        border-radius: 50%;
        background: #fff;
        display: block;
        margin-left:  rem(15px);

        @include tablets{
            margin-left: 0;
            width: rem(7px);
            height: rem(7px);
        }
    }

    &:last-child{
    margin-right: 0;

        &::after{
            display: none;
        }
    }
}

.nav__link{
text-decoration: none;
color: #fff;

@include tablets{
    margin-bottom: rem(16px);
}
}