.video{
    background: url("../img/videobg.png") center center  / cover no-repeat;
}

.container_video{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    @include tablets{
         margin-bottom: rem(82px); 
    }

    @include phones{
        padding: 0 rem(40px);
    }
}

.title-video{
    @include phones{
        text-align: center;
    }
}

.player {
  position: relative;
  display: inline-block;
  height: 395px;
    width: 640px;

  &.hidden {
    opacity: 0;
    transition: opacity 0.3s;
  }
}

.player__splash {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("../img/splash.jpg") center center / cover no-repeat;
  cursor: pointer;
}

.player__wrapper {
  &.active .player__splash {
    display: none;
  }
}


.player__controls {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 35px;
  background: #3e3e3e;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 rem(25px);
}

.player__start {
  width: 0;
  height: 0;
  border: rem(11px) solid transparent;
  border-left-color: #626262;
  padding: 0;
  border-right-width: 0;
  background: transparent;
  outline: none;
  position: relative;
  
  &.paused {
    width: rem(14px);
    height: rem(22px);
    border: none;
    &:after,
    &:before {
      content: "";
      display: block;
      position: absolute;
      width: rem(5px);
      top: 0;
      bottom: 0;
      background: #626262;
    }
    &:after {
      right: 0;
    }
    &:before {
      left: 0;
    }
  }
}

.player__playback {
  height: rem(16px);
  width: rem(446px);
  position: relative;

  &:before {
    content: "";
    height: rem(4px);
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    background: #626262;
    display: block;
    transform: translateY(-50%);
  }
}

.player__playback-button {
  background: #626262;
  border-radius: rem(50px);
  border: none;
  width: rem(12px);
  height: rem(12px);
  padding: 0;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 0;
}

.player__duration {
  color: #626262;
  font-weight: bold;
}