.hamburger__popup {
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(44, 47, 49, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  left: -100%;
  transition: opacity 0.3s;
  opacity: 0;

  &.active {
    left: 0;
    opacity: 1;
  }

  @include tablets {
     &.active .nav__item{
          font-size: rem(24px);
    }
    &.active .header__buttons {
      display: flex;
    }
  }

}

.hamburger__popup-wrap{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;

    @include small{
      padding-top: 20px;
    }
}



.logo__popup{
  position: absolute;
  left: rem(12px);
    top: rem(11px);

    @include small{
      top: rem(20px);
    }
}

.hamburger__popup-close {
  position: absolute;
  right: rem(33px);
  top: rem(30px);

  @include small{
    top: rem(40px);
  }
}
.hamburger__close-svg {
  fill: #c1c2c2;
  width: rem(15px);
  height: rem(15px);
}
