.overlay{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #0005;
    display: flex;
    justify-content: center;
    align-content: center;
    z-index: 2000;
}

.overlayCont{
    position: relative;
}

.overlayContent{
    width: 400px;
    background: #444;
    border-radius: 5px;
    padding: 10px;
    color: #fff;
    font-size: 16px;
    margin: 0;
}
