
.title__delivery{
    text-align: center;
}

.form{
    display: flex;

    @include tablets {
    flex-direction: column;
    padding: 0 rem(149px);
    }

    @include phones{
        padding: 0;
        font-size: rem(16px)
    }
}

.form__side{
    flex: 1;
    margin-right:  rem(20px);

    &:last-child{
        margin-right: 0;
    }
    
    @include tablets{
    flex: 0;
    margin-right: 0;
    }
}


.form__row{
    display: flex;
    margin-bottom:  rem(19px);
}

.form__row-1{
   
    @include phones{
        flex: 1;
    }
}

.form__row-3{
    @include phones{
        flex: 1;
    }
}




.form__block{
    flex: 1;
    margin-right:  rem(20px);
    display: block;
    
    &:last-child{
        margin-right: 0;
    }
    
    &--textarea{
        margin-right: 0;
    }
    
    &:nth-child(3){
        @include phones{
            margin-right: 0;
        }
    }
}

.form__block--textarea{

    @include phones{
        margin-bottom: rem(32px);
    }
}

.form__block-floor{

    @include phones {
        display: none;
        margin: rem(20px);
    }
}
.form__block-title{
    margin-bottom:  rem(7px);
}

.form__input{
    padding:  rem(8px) rem(21px);
    border-radius:  rem(5px);
    border: solid  rem(2px) transparent;
    width: 100%;

    &--textarea{
        resize: none;
        height:  rem(110px);
    }
    &:hover {
    border: solid  rem(2px) $yellow;
}
}

.error{
    color:red;
}